import Link from "next/link";
import { Room } from "@/lib/roomsData";

interface MeetingRoomCardProps {
  room: Room;
  searchParams?: string;
}

export default function MeetingRoomCard({ room, searchParams }: MeetingRoomCardProps) {
  const bookingUrl = searchParams 
    ? `/room/${room.id}?${searchParams}`
    : `/room/${room.id}`;

  return (
    <div className="border border-border rounded-lg p-6 shadow-sm hover:shadow-md transition-shadow bg-card">
      <div className="flex justify-between items-start mb-4">
        <div>
          <h3 className="text-xl font-bold mb-2">{room.name}</h3>
          <p className="text-muted-foreground text-sm mb-1">{room.location}</p>
        </div>
        {room.hourlyRate && (
          <div className="text-right">
            <p className="text-2xl font-bold text-primary">${room.hourlyRate}</p>
            <p className="text-sm text-muted-foreground">per hour</p>
          </div>
        )}
      </div>

      <div className="space-y-3 mb-4">
        <div className="flex items-center justify-between">
          <span className="font-medium">Capacity:</span>
          <span className="text-muted-foreground">{room.capacity} people</span>
        </div>
        
        <div>
          <span className="font-medium block mb-2">Amenities:</span>
          <div className="flex flex-wrap gap-2">
            {room.amenities.map((amenity) => (
              <span 
                key={amenity}
                className="px-2 py-1 bg-secondary text-secondary-foreground text-xs rounded-full"
              >
                {amenity}
              </span>
            ))}
          </div>
        </div>
      </div>

      <p className="text-sm text-muted-foreground mb-4 line-clamp-2">
        {room.description}
      </p>

      <Link 
        href={bookingUrl}
        className="block w-full text-center py-3 bg-primary text-primary-foreground rounded-md font-semibold hover:bg-primary/90 transition-colors focus:outline-none focus:ring-2 focus:ring-ring"
      >
        Book This Room
      </Link>
    </div>
  );
}
