"use client";

import { useSearchParams } from "next/navigation";
import { useEffect, useState } from "react";
import MeetingRoomCard from "@/components/ui/MeetingRoomCard";
import { Room } from "@/lib/roomsData";
import Link from "next/link";

export default function SearchResultsPage() {
  const searchParams = useSearchParams();
  const [rooms, setRooms] = useState<Room[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Extract search parameters for display
  const date = searchParams.get("date");
  const startTime = searchParams.get("startTime");
  const endTime = searchParams.get("endTime");
  const capacity = searchParams.get("capacity");
  const amenities = searchParams.get("amenities");

  useEffect(() => {
    const fetchRooms = async () => {
      try {
        setLoading(true);
        setError(null);
        
        const res = await fetch(`/api/rooms?${searchParams.toString()}`);
        if (!res.ok) {
          throw new Error("Failed to fetch rooms");
        }
        const data = await res.json();
        setRooms(data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchRooms();
  }, [searchParams]);

  const formatTime = (time: string) => {
    return new Date(`2000-01-01T${time}`).toLocaleTimeString([], {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const formatDate = (dateStr: string) => {
    return new Date(dateStr).toLocaleDateString([], {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-muted-foreground">Searching for available rooms...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="py-8">
      {/* Search Summary */}
      <div className="mb-8">
        <div className="flex items-center justify-between mb-4">
          <h1 className="text-3xl font-bold">Search Results</h1>
          <Link 
            href="/"
            className="px-4 py-2 border border-border rounded-md hover:bg-secondary transition-colors"
          >
            New Search
          </Link>
        </div>
        
        <div className="bg-card p-4 rounded-lg border">
          <h2 className="font-semibold mb-2">Search Criteria:</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 text-sm">
            {date && (
              <div>
                <span className="font-medium">Date: </span>
                <span className="text-muted-foreground">{formatDate(date)}</span>
              </div>
            )}
            {startTime && endTime && (
              <div>
                <span className="font-medium">Time: </span>
                <span className="text-muted-foreground">
                  {formatTime(startTime)} - {formatTime(endTime)}
                </span>
              </div>
            )}
            {capacity && (
              <div>
                <span className="font-medium">Capacity: </span>
                <span className="text-muted-foreground">{capacity}+ people</span>
              </div>
            )}
            {amenities && (
              <div>
                <span className="font-medium">Amenities: </span>
                <span className="text-muted-foreground">
                  {amenities.split(',').join(', ')}
                </span>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Error State */}
      {error && (
        <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4 mb-8">
          <h3 className="font-semibold text-destructive mb-2">Error</h3>
          <p className="text-destructive">{error}</p>
        </div>
      )}

      {/* Results */}
      {!error && (
        <>
          <div className="flex items-center justify-between mb-6">
            <p className="text-muted-foreground">
              {rooms.length === 0 
                ? "No meeting rooms found matching your criteria"
                : `Found ${rooms.length} meeting room${rooms.length !== 1 ? 's' : ''} available`
              }
            </p>
            {rooms.length > 0 && (
              <div className="text-sm text-muted-foreground">
                Sorted by capacity (smallest first)
              </div>
            )}
          </div>

          {rooms.length === 0 ? (
            <div className="text-center py-12">
              <div className="text-6xl mb-4">üîç</div>
              <h3 className="text-xl font-semibold mb-2">No rooms found</h3>
              <p className="text-muted-foreground mb-6 max-w-md mx-auto">
                Try adjusting your search criteria. You might want to:
              </p>
              <ul className="text-muted-foreground text-left max-w-md mx-auto mb-6 space-y-1">
                <li>‚Ä¢ Reduce the minimum capacity requirement</li>
                <li>‚Ä¢ Remove some amenity requirements</li>
                <li>‚Ä¢ Try a different date or time</li>
              </ul>
              <Link 
                href="/"
                className="inline-block px-6 py-3 bg-primary text-primary-foreground rounded-md font-semibold hover:bg-primary/90 transition-colors"
              >
                Start New Search
              </Link>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {rooms.map((room) => (
                <MeetingRoomCard 
                  key={room.id} 
                  room={room} 
                  searchParams={searchParams.toString()}
                />
              ))}
            </div>
          )}
        </>
      )}
    </div>
  );
}
